* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #092532;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
    width: 80%;
    margin: 20px auto;
}

.header {
    text-align: center;
    margin: 20px;
    color: #fff;
}

.main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    padding: 20px;
    border: 1px solid #000;
    border-radius: 5px;
    background-color: #fff;
}

.editor-container,
.previewer-container {
    border: 1px solid rgb(114, 105, 105);
    border-radius: 5px;
    padding: 10px;
}

#editor {
    width: 100%;
    height: 100%;
    border: none;
    font-family: inherit;
    font-size: inherit;
    overflow: hidden;
}

#editor:focus {
    outline: none;
}

.footer {
    text-align: center;
    color: #fff;
    margin-top: 10px;
    font-style: italic;
}

blockquote {
    border-left: 3px solid #224b4b;
    color: #224b4b;
    padding-left: 5px;
    margin-left: 25px;
}

code {
    background: white;
    padding: 1px 4px 2px 4px;
    font-size: 12px;
    font-weight: bold;
}

pre {
    background: white;
    padding: 5px 0 5px 5px;
}

#preview h1 {
    border-bottom: 2px solid #224b4b;
}

#preview h2 {
    border-bottom: 1px solid #224b4b;
}

table {
    border-collapse: collapse;
}

td,
th {
    border: 2px solid #224b4b;
    padding-left: 5px;
    padding-right: 5px;
}
